import {toast} from "react-toastify";

export const updateRecipeList = ( recipeItems )  => {
    return {
        type: 'SET_RECIPES_LIST',
        recipesList: recipeItems
    }
};

export const updateIngredientList = ( ingredients )  => {
    return {
        type: 'SET_INGREDIENT_LIST',
        ingredientsList: ingredients
    }
};



export const change_active_nav_tab = ( navItem )  => {
    return {
        type: 'SET_ACTIVE_NAV_PANEL_LIST',
        navItem
    }
};

export const user_auth = ( fullName, picture,isLogged )  => {
    return {
        type: 'USER_AUTH',
        user_full_name: fullName,
        user_picture: picture,
        isLoggedIn: isLogged
    }
}


export const dark_theme = (currentTheme) => {

    return {
        type: 'SWITCH_THEME',
        websiteTheme: currentTheme
    }
}

