import React, {Fragment} from 'react';
import {connect} from 'react-redux';
import { toast } from "react-toastify";
import cx from 'classnames';
import {withRouter} from 'react-router-dom';
import * as actions from '../actions'

import ResizeDetector from 'react-resize-detector';

import AppMain from '../Layout/AppMain';

class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            restaurants: null,
            closedSmallerSidebar: false,
            hideNav : window.width < 1250
        };

    }

    onResize = (width) => {
        const { hideNav } = this.state

        if (hideNav && width > 1250) {
            this.setState({
                hideNav: false
            })
        }else if (!hideNav && width < 1250 ) {
            this.setState({
                hideNav: true
            })
        }

    }


    getRestaurantList = () => {
        const {restaurantsList} = this.props
        if (restaurantsList === undefined) {

            this.props.set_restaurant_list()

        }

    }


    render() {

        let {
            colorScheme,
            enableFixedHeader,
            enableFixedSidebar,
            enableFixedFooter,
            enableClosedSidebar,
            closedSmallerSidebar,
            enableMobileMenu,
            enablePageTabsAlt,
            isLoggedIn
        } = this.props;


        let { hideNav } =  this.state
        return (

                    <Fragment>
                        { isLoggedIn ? this.getRestaurantList() : null }
                        <ResizeDetector
                            handleWidth
                            onResize={this.onResize} />
                        <div className={cx(
                            "app-container app-theme-" + colorScheme,
                            {'fixed-header': enableFixedHeader},
                            {'fixed-sidebar': enableFixedSidebar || hideNav },
                            {'fixed-footer': enableFixedFooter},
                            {'closed-sidebar': enableClosedSidebar || hideNav},
                            {'closed-sidebar-mobile': closedSmallerSidebar || hideNav },
                            {'sidebar-mobile-open': enableMobileMenu},
                            {'body-tabs-shadow-btn': enablePageTabsAlt},
                        )}>
                            <AppMain/>
                        </div>
                    </Fragment>
        )
    }
}

const mapStateToProp = state => ({
    colorScheme: state.ThemeOptions.colorScheme,
    enableFixedHeader: state.ThemeOptions.enableFixedHeader,
    enableMobileMenu: state.ThemeOptions.enableMobileMenu,
    enableFixedFooter: state.ThemeOptions.enableFixedFooter,
    enableFixedSidebar: state.ThemeOptions.enableFixedSidebar,
    enableClosedSidebar: state.ThemeOptions.enableClosedSidebar,
    enablePageTabsAlt: state.ThemeOptions.enablePageTabsAlt,
    restaurantsList: state.ThemeOptions.restaurantsList,
    isLoggedIn: state.ThemeOptions.isLoggedIn
});


export default withRouter(connect(mapStateToProp,actions)(Main));
